:local {
  .clinical-lowlight-theme {
    --terra-time-input-background-size: 0,0;
    --terra-time-input-border-color: #181b1d;
    --terra-time-input-border-radius: 3px;
    --terra-time-input-border-style: solid;
    --terra-time-input-border-width: 1px;
    --terra-time-input-focus-border-style: solid;
    --terra-time-input-focus-background-size: 0 0;
    --terra-time-input-color: #b2b5b6;
    --terra-time-input-disabled-opacity: 0.25;
    --terra-time-input-focus-border-width: 1px;
    --terra-time-input-font-size: 1.14286rem;
    --terra-time-input-line-height: 1.375;
    --terra-time-input-padding-bottom: 0.2142857143rem;
    --terra-time-input-padding-left: 0.2142857143rem;
    --terra-time-input-padding-right: 0.2142857143rem;
    --terra-time-input-padding-top: 0.2142857143rem;
    --terra-time-input-transition-duration: 0.2s;
    --terra-time-input-transition-timing-function: ease;

    --terra-time-input-meridiem-button-background-color: #222a2e;
    --terra-time-input-meridiem-button-border-color: #181b1d;
    --terra-time-input-meridiem-button-color: #b2b5b6;
    --terra-time-input-meridiem-button-font-size: 1rem;
    --terra-time-input-meridiem-button-group-margin-bottom: 0.5rem;
    --terra-time-input-meridiem-button-group-margin-right: 0.5rem;
    --terra-time-input-meridiem-button-height: 2.1428571429rem;
    --terra-time-input-meridiem-button-hover-background-color: #bcbfc0;
    --terra-time-input-meridiem-button-hover-border-color: #d1d1d1;
    --terra-time-input-meridiem-button-hover-color: #000;
    --terra-time-input-meridiem-button-line-height: 1;
    --terra-time-input-meridiem-button-padding-bottom: 0.286rem;
    --terra-time-input-meridiem-button-padding-top: 0.286rem;
    --terra-time-input-meridiem-button-selected-background-color: #1a2023;
    --terra-time-input-meridiem-button-selected-border-color: #181b1d;
    --terra-time-input-meridiem-button-selected-color: #b2b5b6;

    --terra-time-input-invalid-background-color: #222a2e;
    --terra-time-input-invalid-border: 1px solid #fb4c4c;
    --terra-time-input-invalid-box-shadow: 0 0 0 1px #fb4c4c;
    --terra-time-input-invalid-focus-border: 1px solid #004c76;
    --terra-time-input-invalid-focus-box-shadow: 0 0 1px 3px #004c76, 0 0 7px 4px #004c76;
    --terra-time-input-invalid-hover-border: 1px solid #fb4c4c;
    --terra-time-input-invalid-hover-box-shadow: 0 0 1px 3px #fb4c4c, 0 0 7px 4px #fb4c4c;

    --terra-time-input-invalid-disabled-background-color: #222a2e;
    --terra-time-input-invalid-disabled-border: 1px solid #fb4c4c;
    --terra-time-input-invalid-disabled-color: #b2b5b6;
    --terra-time-input-invalid-disabled-opacity: 0.3;

    --terra-time-input-incomplete-background-color: #464232;
    --terra-time-input-incomplete-hover-background-color: #464232;
    --terra-time-input-incomplete-focus-background-color: #464232;
    --terra-time-input-incomplete-focused-input-background-color: #1e3a49;

    --terra-time-input-incomplete-disabled-background-color: #464232;
    --terra-time-input-incomplete-disabled-border: 1px solid #181b1d;  // copied from disabled below
    --terra-time-input-incomplete-disabled-color: #b2b5b6;
    --terra-time-input-incomplete-disabled-opacity: 0.3;

    --terra-time-input-time-component-background-color: transparent;
    --terra-time-input-time-component-background-image: none;
    --terra-time-input-time-component-background-size: 0 0;
    --terra-time-input-time-component-border-color: transparent;
    --terra-time-input-time-component-border-radius: 2px;
    --terra-time-input-time-component-border-style: none;
    --terra-time-input-time-component-border-width: 0;
    --terra-time-input-time-component-caret-color: #b2b5b6;
    --terra-time-input-time-component-color: #b2b5b6;
    --terra-time-input-time-component-focus-background-color: #1e3a49;
    --terra-time-input-time-component-focus-background-size: 0 0;
    --terra-time-input-time-component-focus-color: #b2b5b6;
    --terra-time-input-time-component-focus-ltr-box-shadow: none;
    --terra-time-input-time-component-focus-rtl-box-shadow: none;
    --terra-time-input-time-component-focused-input-background-color: rgba(139, 194, 249, 0.3);
    --terra-time-input-time-component-height: 1.5714285714rem;
    --terra-time-input-time-component-hover-background-color: #1e3a49;
    --terra-time-input-time-component-hover-border: 0 none transparent;
    --terra-time-input-time-component-hover-color: #b2b5b6;
    --terra-time-input-time-component-line-height: 1.375;
    --terra-time-input-time-component-min-width: 4rem;
    --terra-time-input-time-component-padding-bottom: 0;
    --terra-time-input-time-component-padding-left: 0;
    --terra-time-input-time-component-padding-right: 0;
    --terra-time-input-time-component-padding-top: 0;
    --terra-time-input-time-component-transition-duration: 0.2s;
    --terra-time-input-time-component-transition-timing-function: ease;

    --terra-time-input-background-color: #222a2e;
    --terra-time-input-background-image: none;

    --terra-time-input-disabled-background-color: #1d2529;
    --terra-time-input-disabled-border: 1px solid #242b2b;
    --terra-time-input-disabled-color: #404344;

    --terra-time-input-focus-background-color: #1e3a49;
    --terra-time-input-focus-border-color: #181b1d;
    --terra-time-input-focus-color: #b2b5b6;
    --terra-time-input-focus-ltr-box-shadow: 0 0 7px 4px #004c76;
    --terra-time-input-focus-opacity: 1;
    --terra-time-input-focus-rtl-box-shadow: 0 0 7px 4px #004c76;

    --terra-time-input-hover-background-color: #1e3a49;
    --terra-time-input-hover-border: 1px solid #181b1d;
    --terra-time-input-hover-color: #b2b5b6;

    --terra-time-input-meridiem-button-selected-hover-background-color: #1a2023;
    --terra-time-input-meridiem-button-selected-hover-border-color: #181b1d;
    --terra-time-input-meridiem-button-selected-hover-color: #b2b5b6;

    --terra-time-input-time-spacer-line-height: 1.375;
    --terra-time-input-time-spacer-margin-bottom: 0.35714rem;
    --terra-time-input-time-spacer-margin-left: 0.14286rem;
    --terra-time-input-time-spacer-margin-right: 0.14286rem;
    --terra-time-input-time-spacer-margin-top: 0.35714rem;

  }
}
